@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.bonus {
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/background/bonus-mobile.jpg");
  background-repeat: no-repeat;
}

.bonus__description {
  line-height: 26px;
  color: #ffffff;
}

.button {
  background-color: #68b738;
  color: #ffffff;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
}
.button--secondary {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  background-color: #f2f2f2;
  text-transform: uppercase;
  color: #444444;
}

.catalog__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.contacts--white {
  background-color: #ffffff;
}

.contacts__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}

.contacts__description {
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}

.copyright__link {
  text-decoration: none;
}

.extra-card__product {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}

.extra-card__value,
.extra-card__price {
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.extra-catalog__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}

.extra-catalog__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.form__control {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__label {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}

.form__legend {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 24px;
  line-height: 24px;
}

.form__notice {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
}

.main-footer {
  background-color: #f2f2f2;
}

.main-nav__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.main-nav__link {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
}

.main-wrapper__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.page {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  background-color: #ffffff;
  color: #000000;
}

.product-card__link {
  text-decoration: none;
}

.product-card__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
}

.product-card__parameters {
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.product-card__description {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.product-card--more .product-card__title {
  color: #111111;
}

.program-example {
  background-color: #eaeaea;
}

.program-example__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.program-example__description {
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  font-size: 14px;
  line-height: 18px;
}

.program-example__value {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 24px;
  line-height: 24px;
}

.program-example__key {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
}

.program-example__total-price {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.program-type {
  background-color: #f2f2f2;
}

.program-type__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.program-type__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
}

.program-type__description {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.programm-form__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.promo {
  color: #ffffff;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/background/promo-mobile.jpg");
  background-repeat: no-repeat;
}

.promo__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.promo__slogan {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}

.promo__link {
  text-decoration: none;
}

.social__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.social__link {
  text-decoration: none;
}

.work-principle__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.work-principle__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.work-principle__description {
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}